<template>
    <div>
        <div>
            <el-table
                    v-loading="loading"
                    element-loading-text="数据加载中"
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    :data="emps"
                    border stripe
                    size="mini">
                <el-table-column type="selection" width="50" align="left"></el-table-column>
                <el-table-column prop="name" width="120" label="姓名" fixed align="left"></el-table-column>
                <el-table-column prop="workID" width="120" label="工号" align="left"></el-table-column>
                <el-table-column prop="email" width="200" label="电子邮件" align="left"></el-table-column>
                <el-table-column prop="phone" width="130" label="电话号码" align="left"></el-table-column>
                <el-table-column prop="department.name" width="130" label="所属部门" align="left"></el-table-column>
                <el-table-column label="工资账套" align="center">
                    <template slot-scope="scope">
                        <el-tooltip placement="right" v-if="scope.row.salary">
                            <div slot="content">
                                <table>
                                    <tr>
                                        <td>基本工资</td>
                                        <td>{{scope.row.salary.basicSalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>交通补助</td>
                                        <td>{{scope.row.salary.trafficSalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>午餐补助</td>
                                        <td>{{scope.row.salary.lunchSalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>奖金</td>
                                        <td>{{scope.row.salary.bonus}}</td>
                                    </tr>
                                    <tr>
                                        <td>启用时间</td>
                                        <td>{{scope.row.salary.createDate}}</td>
                                    </tr>
                                    <tr>
                                        <td>养老金比率</td>
                                        <td>{{scope.row.salary.pensionPer}}</td>
                                    </tr>
                                    <tr>
                                        <td>养老金基数</td>
                                        <td>{{scope.row.salary.pensionBase}}</td>
                                    </tr>
                                    <tr>
                                        <td>医疗保险比率</td>
                                        <td>{{scope.row.salary.medicalPer}}</td>
                                    </tr>
                                    <tr>
                                        <td>医疗保险基数</td>
                                        <td>{{scope.row.salary.medicalBase}}</td>
                                    </tr>
                                    <tr>
                                        <td>公积金比率</td>
                                        <td>{{scope.row.salary.accumulationFundPer}}</td>
                                    </tr>
                                    <tr>
                                        <td>公积金比率基数</td>
                                        <td>{{scope.row.salary.accumulationFundBase}}</td>
                                    </tr>
                                </table>
                            </div>
                            <el-tag>{{scope.row.salary.name}}</el-tag>
                        </el-tooltip>
                        <el-tag v-else>暂未设置</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <el-popover
                                placement="left"
                                @show="showPop(scope.row.salary)"
                                @hide="hidePop(scope.row)"
                                title="修改工资账套"
                                width="200"
                                trigger="click">
                            <el-select v-model="currentSalary" placeholder="请选择" size="mini">
                                <el-option
                                        v-for="item in salaryies"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        <el-button slot="reference" type="danger" size="mini">修改工资账套</el-button>
                        </el-popover>
                    </template>
                </el-table-column>
            </el-table>
            <div style="margin-top: 10px; display: flex; justify-content: flex-end">
                <el-pagination
                        background
                        layout="sizes, prev, pager, next, jumper, ->, total, slot"
                        @current-change="currentChange"
                        @size-change="sizeChange"
                        :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SalSobCfg",
        data() {
            return {
                emps: [],
                salaryies: [],
                currentSalary: null,
                total: 0,
                currentPage: 1,
                currentSize: 10,
                loading: false
            }
        },
        mounted() {
            this.initEmps();
            this.initSalaryies();
        },
        methods: {
            sizeChange(size) {
                this.currentSize = size;
                this.initEmps();
            },
            currentChange(page) {
                this.currentPage = page;
                this.initEmps();
            },
            hidePop(data) {
                if (this.currentSalary != null) {
                    this.putRequest("/salary/sobcfg/?eid=" + data.id + "&sid=" + this.currentSalary).then(resp => {
                        if (resp) {
                            this.initEmps();
                        }
                    });
                }
            },
            showPop(data) {
                if (data) {
                    this.currentSalary = data.id;
                } else {
                    this.currentSalary = null;
                }

            },
            initSalaryies() {
                this.getRequest("/salary/sobcfg/salary").then(resp => {
                    if (resp) {
                        this.salaryies = resp;
                    }
                });
            },
            initEmps() {
                this.loading = true;
                this.getRequest("/salary/sobcfg/?page=" + this.currentPage + "&size=" + this.currentSize).then(resp => {
                   if (resp) {
                       this.loading = false;
                       this.emps = resp.data;
                       this.total = resp.total;
                   }
                });
            }
        }
    }
</script>

<style scoped>

</style>