<template>
    <div>
        <div style="display: flex; justify-content: space-between;">
            <el-button icon="el-icon-plus" type="primary" size="small" @click="showAddSalarView">添加工资套餐</el-button>
            <el-button icon="el-icon-refresh" size="small" type="success"></el-button>
        </div>
        <div style="margin-top: 10px;">
            <el-table :data="salaryies" border stripe size="small">
                <el-table-column type="selection" width="50"></el-table-column>
                <el-table-column width="120" prop="name" label="账套名称"></el-table-column>
                <el-table-column width="70" prop="basicSalary" label="基本工资"></el-table-column>
                <el-table-column width="70" prop="trafficSalary" label="交通补助"></el-table-column>
                <el-table-column width="70" prop="lunchSalary" label="午餐补助"></el-table-column>
                <el-table-column width="70" prop="bonus" label="奖金"></el-table-column>
                <el-table-column width="100" prop="createDate" label="启用时间"></el-table-column>
                <el-table-column label="养老金" align="center">
                    <el-table-column width="70" prop="pensionPer" label="比率"></el-table-column>
                    <el-table-column width="70" prop="pensionBase" label="基数"></el-table-column>
                </el-table-column>
                <el-table-column label="医疗保险" align="center">
                    <el-table-column width="70" prop="medicalPer" label="比率"></el-table-column>
                    <el-table-column width="70" prop="medicalBase" label="基数"></el-table-column>
                </el-table-column>
                <el-table-column label="公积金" align="center">
                    <el-table-column width="70" prop="accumulationFundPer" label="比率"></el-table-column>
                    <el-table-column width="70" prop="accumulationFundBase" label="基数"></el-table-column>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="small" @click="showEditSalaryView(scope.row)">编辑</el-button>
                        <el-button size="small" type="danger" @click="deleteSalary(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog
                :title="salaryTitle"
                :visible.sync="dialogVisible"
                width="50%">
            <div style="display: flex; justify-content: space-around; align-items: center">
                <el-steps direction="vertical" :active="activeItemIndex">
                    <el-step :title="itemName" v-for="(itemName, index) in salaryItemName" :key="index"></el-step>
                </el-steps>
                <el-input
                          style="width: 200px;"
                          v-model="salary[title]"
                          v-show="activeItemIndex == index"
                          :placeholder="'请输入' + salaryItemName[index] + '...'"
                          v-for="(value, title, index) in salary" :key="index"></el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="preStep">{{activeItemIndex == 10 ? '取消' : '上一步'}}</el-button>
                <el-button type="primary" @click="nextStep">{{activeItemIndex == 10 ? '确定': '下一步'}}</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "SalSob",
        data() {
            return {
                salaryies: [],
                dialogVisible: false,
                activeItemIndex: 0,
                salaryTitle: '添加工资账套',
                salaryItemName: [
                    '账套名称',
                    '基本工资',
                    '交通补助',
                    '午餐补助',
                    '奖金',
                    '养老金比率',
                    '养老金基数',
                    '医疗保险比率',
                    '医疗保险基数',
                    '公积金比率',
                    '公积金基数'
                ],
                salary: {
                    name: '',
                    basicSalary: 0,
                    trafficSalary: 0,
                    lunchSalary: 0,
                    bonus: 0,
                    pensionPer: 0,
                    pensionBase: 0,
                    medicalPer: 0,
                    medicalBase: 0,
                    accumulationFundPer: 0,
                    accumulationFundBase: 0
                }
            }
        },
        mounted() {
            this.initSalaries();
        },
        methods: {
            initSalaries() {
                this.getRequest("/salary/sob/").then(resp => {
                    if (resp) {
                        this.salaryies = resp;
                    }
                });
            },
            showAddSalarView() {
                // 数据初始化
                this.salary = {
                    name: '',
                    basicSalary: 0,
                    trafficSalary: 0,
                    lunchSalary: 0,
                    bonus: 0,
                    pensionPer: 0,
                    pensionBase: 0,
                    medicalPer: 0,
                    medicalBase: 0,
                    accumulationFundPer: 0,
                    accumulationFundBase: 0
                };

                this.salaryTitle = '添加工资账套';
                this.activeItemIndex = 0;
                this.dialogVisible = true;
            },
            showEditSalaryView(data) {
                this.salaryTitle = '修改工资账套';

                this.salary.name = data.name;
                this.salary.basicSalary = data.basicSalary;
                this.salary.trafficSalary = data.trafficSalary;
                this.salary.lunchSalary = data.lunchSalary;
                this.salary.bonus = data.bonus;
                this.salary.pensionPer = data.pensionPer;
                this.salary.pensionBase = data.pensionBase;
                this.salary.medicalPer = data.medicalPer;
                this.salary.medicalBase = data.medicalBase;
                this.salary.accumulationFundPer = data.accumulationFundPer;
                this.salary.accumulationFundBase = data.accumulationFundBase;
                this.salary.id = data.id;

                this.activeItemIndex = 0;
                this.dialogVisible = true;
            },
            nextStep() {
                if (this.activeItemIndex == 10) {
                    if (this.salary.id) {
                        this.putRequest("/salary/sob/", this.salary).then(resp => {
                            if (resp) {
                                this.initSalaries();
                                this.dialogVisible = false;
                            }
                        }) ;
                    } else {
                        this.postRequest("/salary/sob/", this.salary).then(resp => {
                            if (resp) {
                                this.initSalaries();
                                // 关闭窗口
                                this.dialogVisible = false;
                            }
                        });
                    }
                } else {
                    this.activeItemIndex++;
                }
            },
            preStep() {
                if (this.activeItemIndex == 0) {
                    return;
                } else if (this.activeItemIndex == 10) {
                    this.dialogVisible = false;
                    return;
                }

                this.activeItemIndex--;
            },
            deleteSalary(data) {
                this.$confirm('此操作将永久删除【'+ data.name +'】该工资账套, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/salary/sob/" + data.id).then(resp => {
                        if (resp) {
                            this.initSalaries();
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        }
    }
</script>

<style scoped>

</style>